/* Apply the fonts to the component */
.whatwedo {
  font-family: 'Montserrat', sans-serif; /* Default font for the component */
}

.whatwedo .accordion-title {
  font-family: 'Poppins', sans-serif;
}

.whatwedo {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  height: auto; /* Changed to auto for better content flow */
  padding: 40px;
  margin-left: 2%;
  margin-top: 2%;
  background-color: #f9f9f9;
}

.left-section1 {
  flex: 1 1;
  padding-right: 20px;
}

.left-section1 h1 {
  font-size: 2em;
  margin: 20px 0;
  font-weight: 700;
}

.left-section1 h2 {
  font-size: 1.2em;
  color: #666;
  font-weight: 400;
}

.accordion {
  margin-top: 20px;
}

.accordion-item {
  margin-bottom: 10px;
  border-bottom: 1px solid #ddd;
}

.accordion-title {
  padding: 10px;
  font-size: 1em;
  cursor: pointer;
  background-color: #fff;
  transition: background-color 0.3s;
  font-weight: 400;
}

.accordion-title:hover,
.accordion-title.active {
  background-color: #efefef;
}

.accordion-content {
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.3s ease, padding 0.3s ease;
}

.accordion-content.open {
  max-height: 500px; /* Increased max-height for better display */
  padding: 10px;
  background-color: #fafafa;
}

.right-section1 {
  flex: 1 1;
  display: flex;
  justify-content: center;
  align-items: center;
  left: 5%;
}

.right-section1 img {
  max-width: 75%;
  border-radius: 10%;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.right-section1 {
  position: relative;
}

.image-container {
  position: relative;
  display: inline-block;
}

.image-text {
  position: absolute;
  top: 90%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  color: rgb(1, 0, 0);
}

.image-text p {
  font-size: 20px;
  font-family: 'Lato', sans-serif;
  font-weight: bold;
  margin: 0;
}

/* Add responsive styles */
@media screen and (max-width: 1213px) and (min-width: 750px) {
  .whatwedo {
    flex-direction: column;
    align-items: center;
    height: auto;
  }

  .left-section1 {
    padding-right: 0;
  }

  .right-section1 {
    margin-top: 20px;
  }

  .accordion-content.open {
    max-height: 600px; /* Further increased max-height */
  }
}

@media screen and (max-width: 768px) {
  .whatwedo {
    flex-direction: column;
  }

  .left-section1 h1 {
    font-size: 1.8em;
  }

  .left-section1 h2 {
    font-size: 1em;
  }

  .accordion-title {
    font-size: 0.9em;
  }

  .accordion-content.open {
    max-height: 500px; /* Further increased max-height */
  }

  .right-section1 {
    order: 2; /* Ensure image is displayed below accordion content */
    margin-top: 20px;
  }
}

@media screen and (max-width: 480px) {
  .whatwedo {
    flex-direction: column;
  }

  .left-section1 h1 {
    font-size: 1.5em;
  }

  .left-section1 h2 {
    font-size: 0.9em;
  }

  .accordion-title {
    font-size: 0.8em;
  }

  .accordion-content.open {
    max-height: 400px; /* Adjusted max-height for smaller screens */
  }

  .right-section1 img {
    max-width: 100%;
  }

  .right-section1 {
    order: 2; /* Ensure image is displayed below accordion content */
    margin-top: 20px;
  }
}

.health-account {
  text-align: center;
  padding: 20px;
}

.peek {
  color: #9bdfd9;
  font-size: 1em;
  margin-bottom: 10px;
}

h1 {
  font-size: 2em;
  margin-bottom: 40px;
}

.cards {
  display: flex;
  justify-content: center;
  grid-gap: 20px;
  gap: 20px; /* Add some gap between cards */
  flex-wrap: wrap; /* Allow cards to wrap */
}

.card {
  flex: 1 1;
  max-width: 300px;
  height: 450px;
  border-radius: 20px;
  padding: 10px;
  text-align: left;
  position: relative;
  background-size: cover;
  background-position: center;
  transition: transform 0.3s ease;
}

.card:hover {
  transform: scale(1.1);
}

.card h2 {
  font-size: 1.5em;
  margin-bottom: 10px;
}

.card p {
  font-size: 1em;
  margin-bottom: 20px;
}

.card button {
  background: none;
  border: 2px solid #000;
  border-radius: 5px;
  padding: 10px 20px;
  cursor: pointer;
  font-size: 1em;
  font-weight: bold;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.card button span {
  margin-left: 10px;
}

.card .image {
  position: absolute;
  bottom: 20px;
  right: 20px;
}

.card .image img {
  width: 100px;
  height: 100px;
}

/* Specific color classes */
.blue {
  background-image: url(https://i.postimg.cc/6q6Q2Vw9/1000152258.jpg);
  background-color: #a8e6cf;
  color: #000;
}

.green {
  background-image: url(https://i.postimg.cc/XJ5CnkLX/1000152250.jpg);
  background-color: #dcedc1;
  color: #000;
}

.yellow {
  background-image: url(https://i.postimg.cc/mkRNqn4T/1000152254.jpg);
  background-color: #ffeb3b;
  color: #000;
}

/* Responsive styles */

@media screen and (max-width: 1200px) {
  .card {
    flex-basis: calc(50% - 20px); /* Two cards per row */
    max-width: calc(50% - 20px);
  }
}

@media screen and (max-width: 768px) {
  .card {
    flex-basis: calc(100% - 20px); /* One card per row */
    max-width: calc(100% - 20px);
  }
}

